@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900&display=swap);
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Poppins', sans-serif;
}

section {
   position: relative;
   min-height: 100vh;
   background-color: #0062cc;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
   -webkit-align-items: center;
           align-items: center;
   padding: 20px;
}

section .container {
   position: relative;
   width: 800px;
   height: 500px;
   background: #fff;
   box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
   overflow: hidden;
   border-radius: 10px;
}

section .container .user {
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   display: -webkit-flex;
   display: flex;
   border-radius: 10px;
}

section .container .user .imgBx {
   position: relative;
   width: 50%;
   height: 100%;
   background: #0063ccf5;
   transition: 0.5s;
}

section .container .user .imgBx img {
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   object-fit: cover;
}

section .container .user .formBx {
   position: relative;
   width: 50%;
   height: 100%;
   background: #fff;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
   -webkit-align-items: center;
           align-items: center;
   padding: 40px;
   transition: 0.5s;
}

section .container .user .formBx form h2 {
   font-size: 18px;
   font-weight: 600;
   text-transform: uppercase;
   letter-spacing: 2px;
   text-align: center;
   width: 100%;
   margin-bottom: 10px;
   color: #555;
}

section .container .user .formBx form input {
   position: relative;
   width: 100%;
   padding: 10px;
   background: #f5f5f5;
   color: #333;
   border: none;
   outline: none;
   box-shadow: none;
   margin: 8px 0;
   font-size: 14px;
   letter-spacing: 1px;
   font-weight: 300;
   border-radius: 6px;
}

section .container .user .formBx form input[type='submit'] {
   max-width: 130px;
   background: #0062cc;
   color: #fff;
   cursor: pointer;
   font-size: 14px;
   font-weight: 500;
   letter-spacing: 1px;
   transition: background 0.5s ease;
   border-radius: 6px;
}

section .container .user .formBx form input[type='submit']:hover {
   background: #0063ccf5;
}

section .container .user .formBx form .signup {
   position: relative;
   margin-top: 20px;
   font-size: 12px;
   letter-spacing: 1px;
   color: #555;
   text-transform: uppercase;
   font-weight: 300;
}

section .container .user .formBx form .signup a {
   font-weight: 600;
   text-decoration: none;
   color: #677eff;
}

section .container .signupBx {
   pointer-events: none;
}

section .container.active .signupBx {
   pointer-events: initial;
}

section .container .signupBx .formBx {
   left: 100%;
}

section .container.active .signupBx .formBx {
   left: 0;
}

section .container .signupBx .imgBx {
   left: -100%;
}

section .container.active .signupBx .imgBx {
   left: 0%;
}

section .container .signinBx .formBx {
   left: 0%;
}

section .container.active .signinBx .formBx {
   left: 100%;
}

section .container .signinBx .imgBx {
   left: 0%;
}

section .container.active .signinBx .imgBx {
   left: -100%;
}

@media (max-width: 991px) {
   section .container {
      max-width: 400px;
   }

   section .container .imgBx {
      display: none;
   }

   section .container .user .formBx {
      width: 100%;
   }
}

html {
   height: 100vh;
   width: 100vw;
}

/* Navbar */
.bg-dark {
   background-color: #0062cc !important;
}

/* Calendario */
.calendar-screen {
   display: -webkit-flex;
   display: flex;
   -webkit-flex-flow: column;
           flex-flow: column;
   height: 100vh;
}

.rbc-calendar {
   height: 100%;
}

/* Modal */
.ReactModalPortal > div {
   opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
   -webkit-align-items: center;
           align-items: center;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
   transition: opacity 0.2s ease-in-out;
   z-index: 999;
}

.modal-fondo {
   background-color: rgba(0, 0, 0, 0.3);
   bottom: 0;
   left: 0;
   right: 0;
   top: 0;
   position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
   opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
   opacity: 0;
}

.modal {
   background: white;
   border-radius: 5px;
   color: rgb(51, 51, 51);
   display: inline;
   max-height: 620px;
   max-width: 500px;
   outline: none;
   padding: 10px;
}

/* Datepicker */
.react-datetime-picker input:focus {
   outline: none;
}

.react-datetime-picker__wrapper {
   border: 0px solid gray !important;
}

/* FABS bot贸n agregar nuevo evento */
.fab {
   border-radius: 100%;
   bottom: 25px;
   font-size: 25px;
   padding: 18px;
   position: fixed;
   right: 25px;
}

/* FABS borrar evento activo */
.fab-danger {
   bottom: 25px;
   font-size: 25px;
   padding: 10px;
   position: fixed;
   left: 25px;
}

/* Loader */
.loader {
   -webkit-animation: spin 1.5s linear infinite;
           animation: spin 1.5s linear infinite;
   border: 4px solid rgba(0, 0, 0, 0.1);
   border-radius: 50%;
   border-left-color: #0062cc;
   bottom: 0;
   height: 36px;
   left: 50%;
   position: fixed;
   right: 0;
   top: 50%;
   width: 36px;
}

.loader-background {
   height: 100vh;
   position: absolute;
   min-width: 100vw;
   min-height: 100vh;
   max-width: 100vw;
   max-height: 100vh;
   width: 100vw;
   z-index: 9999;
}

@-webkit-keyframes spin {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
   }
}

@keyframes spin {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
   }
}

@media (max-width: 320px) {
   /* Modal */
   .modal-h1 {
      font-size: 30px;
   }
   .modal {
      max-height: 100vh;
      max-width: 100vw;
      overflow-y: scroll;
   }

   .modal::-webkit-scrollbar {
      -webkit-appearance: none;
   }

   /* Datepicker */
   .react-datetime-picker {
      padding-left: 2px;
      font-size: 15px;
   }

   /* FABS bot贸n agregar nuevo evento */
   .fab {
      padding: 16px;
   }

   /* FABS borrar evento activo */
   .fab-danger {
      font-size: 22px;
   }
}

